<template>
  <div id="app">
    <el-header>
      <h1 style="text-align: center">
      网关管理系统
      </h1>
    </el-header>
    <el-main>
      <el-card class="box-card">
        <el-row>
        <el-col :span="24">
        <el-form :inline="true" :model="formInline" class="demo-form-inline" >
          <el-form-item  label="服务ID" style="margin-left: 50px">
            <el-input v-model="formInline.user" placeholder="服务ID"></el-input>
          </el-form-item>
          <el-form-item  label="访问路径" style="margin-left: 50px">
            <el-input v-model="formInline.region" placeholder="访问路径"></el-input>
          </el-form-item>
          <el-form-item style="margin-left: 50px">
            <el-button type="success" icon="el-icon-search" @click="onRefresh(formInline)">查询</el-button>
          </el-form-item>
          <el-form-item style="margin-left: 20px">
            <el-button type="success" icon="el-icon-star-off" @click="onReset()">重置</el-button>
          </el-form-item>
        </el-form>
        </el-col>
        </el-row>
        <el-row>
        <el-col :span="6">
          <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd('form')">新建</el-button>
        </el-col>
          <el-col :span="18">

          </el-col>
        </el-row>
        <el-dialog v-if="createClick"  title="网关管理" :visible.sync="createClick" :close-on-click-modal="false" :before-close="handleClose">
          <el-form ref="form" :model="form"  :rules="rules"label-width="80px">
            <el-form-item prop="id">
              <el-col :span="4">网关ID :</el-col>
              <el-col :span="2"></el-col>
              <el-col :span="8" v-if="xj"><el-input v-model="form.id"></el-input></el-col>
              <el-col :span="8" v-else><el-input v-model="form.id" :disabled="true"></el-input></el-col>
              <el-col :span="10"></el-col>
            </el-form-item>
            <el-form-item >
              <el-col :span="4">服务ID :</el-col>
              <el-col :span="2"></el-col>
              <el-col :span="8"><el-input v-model="form.serviceId"></el-input></el-col>
              <el-col :span="10"></el-col>
            </el-form-item>
            <el-form-item prop="serviceId">
              <el-col :span="4">转发地址URL :</el-col>

              <el-col :span="16" style="padding-left: 20px"> lb: // {{form.serviceId}}</el-col>
              <el-col :span="4"></el-col>
            </el-form-item>
            <el-form-item prop="predicates">
              <el-col :span="4">访问路径 :</el-col>
              <el-col :span="2"></el-col>
              <el-col :span="14"><el-input v-model="form.predicates"></el-input></el-col>
              <el-col :span="4"></el-col>
            </el-form-item>
            <el-form-item >
              <el-col :span="4">过滤 :</el-col>
              <el-col :span="2"></el-col>
              <el-col :span="14"><el-input v-model="form.filters"></el-input></el-col>
              <el-col :span="4"></el-col>
            </el-form-item>
            <el-form-item >
              <el-col :span="4">顺序号 :</el-col>
              <el-col :span="2"></el-col>
              <el-col :span="14"><el-input v-model="form.orders"></el-input></el-col>
              <el-col :span="4"></el-col>
            </el-form-item>
            <el-input v-model="form.bjid" style="display:none"></el-input>
            <el-form-item>
              <el-col :span="4">备注 :</el-col>
              <el-col :span="2"></el-col>
              <el-col :span="14"><el-input type="textarea" :rows="4" v-model="form.remarks"></el-input></el-col>
              <el-col :span="4"></el-col>
            </el-form-item>
            <el-form-item>
              <el-row style="float:right" >
              <el-button type="primary" @click="onSubmit">保存</el-button>
               <el-button @click="onCancel()">取消</el-button>
              </el-row>
            </el-form-item>
          </el-form>
        </el-dialog>
        <template>
          <el-table
            :data="list"
            style="width: 100%"
            :row-class-name="tableRowClassName">
            <el-table-column
              prop="id"
              label="网关ID"
              width="210">
            </el-table-column>
            <el-table-column
              prop="serviceId"
              label="服务ID"
              width="210">
            </el-table-column>
            <el-table-column
              prop="uri"
              label="转发网址URL">
            </el-table-column>
            <el-table-column
              prop="predicates"
              label="访问路径">
            </el-table-column>
            <el-table-column
              prop="filters"
              label="过滤">
            </el-table-column>
            <el-table-column
              prop="orders"
              label="顺序号"
               v-if="type">
            </el-table-column>
            <el-table-column
              prop="remarks"
              label="备注"
              v-if="type">
            </el-table-column>
            <el-table-column
              prop="cz"
              label="操作">
              <template slot-scope="scope">
                <el-button type="primary" size="mini" @click="onEdit(scope.row)">编辑</el-button>

                <el-button type="danger" size="mini" @click="onDelect(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>

        </template>
      </el-card>
    </el-main>



  </div>
</template>

<script>

import axios from 'axios'


  export default {
    data() {

      return {
        xj:'true',
        bj:'1',
        createClick:false,
        type:false,
        form:{
          id:'',
          serviceId:'',
          uri:'',
          predicates:'',
          filters:'1',
          orders:'',
          remarks:'',
          bjid:''
        },
        rules: {
          id: [
            { required: true, message: '请填写网关ID', trigger: 'blur' },

          ],
          serviceId: [
            { required: true, message: '请填写服务ID', trigger: 'blur' },

          ],
          predicates: [
            { required: true, message: '请填写访问路径', trigger: 'blur' },

          ],
        },
        formInline: {
          user: '',
          region: ''
        },
        list: [],
      }
    },
    created(){
      this.getlist()
    },
    methods: {
      onReset(){
        this.formInline.user='',
        this.formInline.region=''
        this.getlist()
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            this.form.id='',
              this.form.serviceId='',
              this.form.predicates='',
              this.form.filters='',
              this.form.orders='',
              this.form.remarks='',
              this.form.uri=''
              this.getlist()
            done();
          })
          .catch(_ => {});
      },
      // serviceId1(){
      //   this.form.serviceId =this.form.uri
      // },
      //this.$http.post
      getlist(){
        this.$http.post('/jbdp-route/query').then(res => {
           this.list= res.data
        }).catch((err) => {
          this.$message.error(err);
        })
      },
      //添加
      onAdd(){
      this.createClick=true
      },
      //编辑
      onEdit(row){
        this.xj=false
        console.log(row)
        this.bj='2'
        this.form=row
        this.createClick=true

      },
      //保存
      onSubmit() {
        //新建
        console.log(this.form.bjid)
        if(this.bj!='2'){
          this.form.uri=this.form.serviceId
          this.$http.post('/jbdp-route/add', this.form
        ).then(res => {
          this.$message({
            offset:'300',
            message: '保存成功！',
            type: 'success'
          });
          this.getlist()
          this.onCancel()
        }).catch((err) => {
          this.onCancel()
          offset:'300',
          this.$message.error('保存失败');
        })}
        //编辑
       if(this.bj=='2'){
         this.form.uri=this.form.serviceId
         this.$http.post('/jbdp-route/update', this.form
         ).then(res => {
           this.$message({
             offset:'300',
             message: '编辑成功！',
             type: 'success'
           });
           this.getlist()
           this.onCancel()
         }).catch((err) => {
           this.onCancel()
           offset:'300',
           this.$message.error('编辑失败');
         })
       }
      },
      //取消
      onCancel(){
          this.form.id='',
          this.form.serviceId='',
          this.form.predicates='',
          this.form.filters='',
          this.form.orders='',
          this.form.remarks=''
          this.form.uri=''
          this.createClick=false

        // debugger
        // console.log(form)
        // console.log('11111111')
        // this.$nextTick(()=>{
        //   this.$refs[form].resetFields();
        // })
      },

      //删除
      onDelect(row){
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post('/jbdp-route/delete/'+row.id).then(res => {
            this.getlist()
          }).catch((err) => {
            this.$message.error(err);
          })
          this.$message({
            offset:'300',
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            offset:'300',
            type: 'info',
            message: '已取消删除'
          });
        });

      },
      onRefresh(formInline){
        this.$http.post('/jbdp-route/query/?serviceId='+this.formInline.user+"&routePath="+this.formInline.region,)
          .then(res => {
          this.list= res.data

        }).catch((err) => {
          this.$message.error(err);
        })
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      }
    }
  }
</script>

<style>
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>



